import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable, of } from 'rxjs';
import { RxFeatureService } from '../services/feature/feature.service';

@Injectable({
  providedIn: 'root'
})
export class RxFeatureGuard implements CanActivate {
  constructor(private rxFeatureService: RxFeatureService) {}

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<UrlTree | boolean> {
    return of(this.rxFeatureService.isFeatureEnabled(route.data['featureId']));
  }
}
